@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-header {
  min-height: 658px;
  padding: 30px 50px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, hsl(347deg, 100%, 36%) -46.69deg, hsl(212deg, 100%, 26%) 32.24deg, hsl(347deg, 100%, 36%) 313.31deg, hsl(212deg, 100%, 26%) 392.24deg);
}
@media (max-width: 1199px) {
  .main-header {
    min-height: 554px;
    padding: 40px 45px;
  }
}
@media (max-width: 767px) {
  .main-header {
    min-height: 491px;
    padding: 15px;
  }
}

.main-header__intro {
  position: relative;
}

.main-header__navigation {
  position: relative;
  z-index: 2;
}

.container {
  max-width: 1260px;
  margin: 0 auto;
}
.container--about {
  display: grid;
  grid-template-columns: 355px 1fr;
  gap: 85px;
  align-items: center;
  padding: 120px 50px 120px 110px;
}
@media (max-width: 1199px) {
  .container--about {
    display: block;
    padding: 100px 45px;
  }
}
@media (max-width: 767px) {
  .container--about {
    padding: 70px 0;
  }
}
.container--membership {
  position: relative;
  padding: 120px 50px;
  text-align: center;
}
@media (max-width: 1199px) {
  .container--membership {
    padding: 100px 45px;
  }
}
@media (max-width: 767px) {
  .container--membership {
    padding: 70px 15px;
  }
}
.container--promo {
  display: grid;
  grid-template-columns: 1fr 660px;
  justify-content: center;
  padding: 120px 50px 50px;
}
@media (max-width: 1199px) {
  .container--promo {
    grid-template-columns: 1fr 2fr;
    padding: 100px 45px;
  }
}
@media (max-width: 767px) {
  .container--promo {
    grid-template-columns: 1fr;
    padding: 70px 15px 35px;
  }
}

.navigation-header {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .navigation-header {
    padding: 0 0 25px;
  }
}

.navigation-header__link {
  text-align: end;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .navigation-header__link {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .navigation-header__link {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

@media (max-width: 1199px) {
  .logo__icon {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .logo__icon {
    width: 69px;
    height: 24px;
  }
}

.intro {
  display: flex;
  gap: 64px;
  margin: -30px 0 0;
  padding: 0 19px;
}
@media (max-width: 1199px) {
  .intro {
    gap: 30px;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .intro {
    display: block;
  }
}
@media (max-width: 767px) {
  .intro picture {
    display: none;
  }
}

.intro__text {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  max-width: 400px;
  margin: 110px 0 0;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .intro__text {
    max-width: 324px;
    margin: 56px 0 0;
    column-gap: 25px;
  }
}
@media (max-width: 767px) {
  .intro__text {
    max-width: 100%;
    column-gap: 0;
  }
}

.intro__list {
  min-width: 113px;
  align-self: center;
  justify-self: start;
}
@media (max-width: 767px) {
  .intro__list {
    justify-self: end;
    margin: 100px -10px 0 0;
  }
}

.intro__item {
  text-align: end;
  font-size: 14px;
  line-height: 21px;
}

.intro__city {
  display: flex;
  justify-content: end;
  margin: 0 0 30px 100px;
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .intro__city {
    margin: 0 0 15px 0;
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .intro__city {
    font-size: 28px;
    line-height: 34px;
  }
}

.intro__title {
  margin: 0;
  text-align: end;
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1199px) {
  .intro__title {
    text-transform: none;
    font-size: 72px;
    line-height: 86px;
  }
}

.intro__button {
  grid-column: 2/3;
  align-self: start;
  width: 230px;
  padding: 17px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 4px solid #ffffff;
}
@media (max-width: 1199px) {
  .intro__button {
    width: 183px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    grid-column: 1/3;
    width: 100%;
  }
}

@media (max-width: 1199px) {
  .intro__image {
    min-width: 324px;
    min-height: 390px;
  }
}

.title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
