.title--about {
  font-size: 48px;
  line-height: 58px;
}

.about__text {
  color: #102654;
}
@media (max-width: 1199px) {
  .about__text {
    text-align: center;
    padding: 0 88px;
  }
}
@media (max-width: 767px) {
  .about__text {
    text-align: start;
    padding: 0 15px;
  }
}
.about__text span {
