  .about__text span {
    margin: 0 0 50px;
  }
}
@media (max-width: 767px) {
  .about__text span {
    margin: 0 0 40px;
  }
}
.about__text p {
  margin: 0 0 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
