.about__title {
  margin: 0 0 25px;
}
@media (max-width: 1199px) {
  .about__title {
    margin: 0 0 6px;
  }
}
@media (max-width: 767px) {
  .about__title {
    margin: 0 0 20px;
  }
}

.about__media {
