}
@media (max-width: 1199px) {
  .about__media {
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .about__media {
    display: block;
  }
}

@media (max-width: 767px) {
  .about__image {
    width: 100%;
    aspect-ratio: 320/313;
  }
}

.about__video {
  position: absolute;
  bottom: -130px;
