  width: 364px;
  height: 228px;
}
@media (max-width: 1199px) {
  .video {
    width: 266px;
    height: 167px;
  }
}
@media (max-width: 767px) {
  .video {
    width: 100%;
    height: auto;
    aspect-ratio: 320/167;
  }
}

.video__link {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.video__link::before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(20, 54, 122, 0.5);
}

.video__image {
  width: 100%;
  height: 100%;
}

.video__button {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/svg/play.svg");
  cursor: pointer;
}
.video__button:hover {
  opacity: 0.5;
}

