}
@media (max-width: 767px) {
  .membership__list-card {
    padding: 0;
  }
}

.membership__image {
  position: absolute;
  bottom: -92px;
  right: -162px;
  display: block;
  transform: rotate(150deg);
}
@media (max-width: 1199px) {
  .membership__image {
    bottom: -77px;
    right: -11px;
