.card {
  padding: 68px 30px;
  background-color: #ffffff;
  border: 7px solid transparent;
}
.card:hover {
  border-image: linear-gradient(135deg, #d10a42 0%, #00359f 100%);
  border-image-slice: 1;
}
.card:hover .card__button {
  outline: 0;
  color: #ffffff;
  background: linear-gradient(97.05deg, #ed0335 17.6%, #003eb7 95.31%);
}

.card__title {
  margin: 0 0 22px;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  color: #102654;
}
@media (max-width: 767px) {
  .card__title {
    font-size: 22px;
    line-height: 24px;
  }
}

.card__description {
  margin: 0 0 59px;
  font-size: 16px;
  line-height: 24px;
  color: #495a7d;
}

.card__price {
  position: relative;
  margin: 0 auto 120px;
}
.card__price::before {
  content: attr(data-parent);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
  font-weight: 700;
  font-size: 100px;
  line-height: 69px;
  color: #e8e8e8;
  opacity: 0.48;
}

.card__button {
  padding: 27px 44px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #102654;
  outline: 2px solid #102654;
}

.price__sum {
  position: relative;
  top: 36px;
  left: 0;
  z-index: 1;
  width: max-content;
  margin: 0 auto;
  padding-right: 44px;
  font-weight: 700;
  font-size: 60px;
  line-height: 42px;
  color: #103aae;
}
.price__sum::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 42px;
  background-image: url("../img/sprite/rouble.svg");
  background-repeat: no-repeat;
}

.promo__text {
  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 50px;
  row-gap: 4px;
  padding: 85px 78px;
  background-color: #ed0233;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .promo__text {
    grid-template-columns: 117px 56px;
    grid-template-rows: min-content min-content min-content;
    column-gap: 29px;
    row-gap: 0;
    padding: 51px 45px;
  }
}
@media (max-width: 767px) {
  .promo__text {
    justify-content: center;
    padding: 50px 44px;
  }
}
.promo__text dl {
  display: flex;
  flex-direction: column;
  margin: 0;
}
.promo__text dt {
  width: max-content;
  padding: 0 27px 13px 0;
  border-bottom: 1px solid #ffffff;
  font-size: 18px;
  line-height: 27px;
}
.promo__text dt:last-of-type {
  padding: 0 42px 13px 0;
}
@media (max-width: 1199px) {
  .promo__text dt:last-of-type {
    padding: 0 24px 7px 0;
  }
}
@media (max-width: 1199px) {
  .promo__text dt {
    padding: 0 17px 7px 0;
    font-size: 12px;
    line-height: 18px;
  }
}
.promo__text dd {
  display: flex;
  align-items: center;
  gap: 7px;
  margin: 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
}
@media (max-width: 1199px) {
  .promo__text dd {
    font-size: 24px;
    line-height: 36px;
  }
}
.promo__text p {
  grid-column: 1/3;
  margin: 0;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1199px) {
  .promo__text p {
    font-size: 12px;
    line-height: 18px;
  }
}
.promo__text .promo__button {
  grid-column: 1/3;
  margin: 20px 0 0;
  padding: 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: inherit;
  border: 4px solid #ffffff;
}
@media (max-width: 1199px) {
  .promo__text .promo__button {
    margin: 20px 0 0;
    padding: 11px;
    font-size: 10px;
    line-height: 12px;
    border: 2px solid #ffffff;
  }
}

.promo__title {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 82px;
  line-height: 79px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .promo__title {
    font-size: 48px;
    line-height: 46px;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
}
.promo__wrapper span {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
@media (max-width: 1199px) {
  .promo__wrapper span {
    font-size: 10px;
    line-height: 15px;
  }
}

.promo__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

